// SEARCH :: START

.search {
  width: 265px;


  &__form {
    display: flex;
  }


  &__field {
    position: relative;
    height: 30px;

    &:nth-child(1) {
      width: 100%;
      max-width: 225px;
      margin-right: 10px;
    }
  }


  &__input {
    outline: none;
    width: 100%;
    height: 100%;
    padding-left: 12px;
    padding-right: 33px;
    border: none;
    border-radius: 4px;
    box-shadow: 0 2px 4px 0 rgba($c-black, 0.1);
    font-family: "Open Sans", sans-serif;

    &::-webkit-input-placeholder {
      color: rgba($c-black, 0.2);
      font-family: "Open Sans", sans-serif;
    }
    &::-moz-placeholder {
      color: rgba($c-black, 0.2);
      font-family: "Open Sans", sans-serif;
    }
    &:-ms-input-placeholder {
      color: rgba($c-black, 0.2);
      font-family: "Open Sans", sans-serif;
    }
    &:-moz-placeholder {
      color: rgba($c-black, 0.2);
      font-family: "Open Sans", sans-serif;
    }
  }


  &__label {
    cursor: pointer;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    right: 0;
    width: 33px;
    height: 100%;

    &-icon {
      @include retina-sprite($sp-search-label-group)
    }
  }


  &__btn {
    outline: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    border: none;
    border-radius: 4px;
    background-color: $c-darkCerulean;

    &:hover,
    &:focus {
      background-color: $c-putty;
      @include transition(background-color);
    }

    &-icon {
      @include retina-sprite($sp-search-group)
    }
  }
}

// SEARCH :: END